.navigation {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;

  .nav-links-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .nav-link {
      padding: 10px 15px;
      text-decoration: none;
      cursor: pointer;
    }
  }

  .logo-container {
    height: 100%;
    width: 100%;

    h1 {
      font-family: "Indie Flower", cursive;
    }
  }
}
